<template lang="html">
  <div class="card">
    <picture-box :images="images"></picture-box>
    <div class="card-content">
      <div class="media">
        <div class="media-content">
          <p class="title is-4">{{title}}</p>
        </div>
      </div>

      <div class="content">
        {{description}}
      </div>
    </div>
    <footer class="card-footer">
      <a class="card-footer-item button is-white">
        مشاهده
      </a>
      <a class="card-footer-item button is-white">
        خرید
      </a>
    </footer>
  </div>
</template>

<script >
  import PictureBox from '../PictureBox';

  export default  {
    name: 'PictorialShopItem',
    props: {
      images: {
        type: Array
      },
      title: {
        type: String
      },
      description: {
        type: String
      },
      price: {
        type: String
      },
      unknownURL: {
        default: 'data:image/png;base64,',
        type: String
      }
    },
    data: function() {
      return {
      }
    },
    components: {
      PictureBox
    },
    methods: {
    }
  }
</script>

