<template lang="html">
  <div class="card">
    <div class="card-image">
      <picture-box :image="'cdn/' + image"></picture-box>
      <div class="columns is-mobile is-vcentered is-multiline is-overlay sample-item-overlay" style="margin: 0px;">
        <div class="column is-full " style="height: 20%;">
          <div class="columns" style="padding: 0px; margin: 0px;">
            <div class="column" style="padding: 0px; margin: 0px;">
              <span><i class="fa fa-edit sample-item-overlay-button" v-on:click="editClick" aria-hidden="true"></i></span>
              <span><i class="fa fa-trash sample-item-overlay-button" v-on:click="deleteClick" aria-hidden="true"></i></span>
            </div>
          </div>
        </div>
        <div class="column is-full " style="height: 80%;">&nbsp;</div>
      </div>
    </div>
    <div class="card-content">
      <div class="media media-title">
        <div class="media-content">
          <p class="title is-4">{{title}}</p>
        </div>
      </div>

      <div class="content" style="overflow-x: hidden; overflow-y: auto; height: 72px;">
        {{description}}
      </div>
    </div>
    <footer class="card-footer">
<!--      <router-link to="/link" class="card-footer-item button is-white">-->
      <a class="card-footer-item button is-white" v-bind:href="'#' + link">
        {{defaultCaption}}
      </a>
<!--      </router-link>-->
    </footer>
  </div>
</template>

<script lang="js">
  import PictureBox from '../PictureBox'

    export default {
      name: 'PictorialSampleItem',
        props: {
            id: {
                type: Number
            },
            link: {
                type: String
            },
            images: {
                type: Array
            },
            image: {
                type: String
            },
            caption: {
                type: String
            },
            title: {
                type: String
            },
            description: {
                type  : String
            },
            unknownURL: {
                default: 'data:image/png;base64,',
                type: String
            }
        },
        data: function() {
            return {
            }
        },
        components: {
          PictureBox
        },
        methods: {
            editClick: function () {
              console.log("edit", this.id);
              this.$emit("edit-item", this.id);
            },
            deleteClick: function () {
                console.log("delete");
                this.$emit("delete-item", this.id);
            }
        },
        computed: {
            defaultCaption() {
                if (!this.caption)
                    return 'مشاهده';
                return this.caption;
            }
        }
    };
</script>

<style scoped lang="css">
  .sample-item-overlay {
    z-index: 10;
  }

  .sample-item-overlay-button {
    color: #c69500;
    cursor: pointer;
  }
  .sample-item-overlay-button:hover {
    background-color: #c2e0f5;
    padding: 4px;
    border-radius: 3px;
  }

  ::-webkit-scrollbar {
    width: 12px;
  }

  ::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
  }

  ::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
  }

  .media-title {
    padding-top: 18px;
    padding-bottom: 10px;
  }
</style>
