<template lang="html">
  <div class="card">
    <picture-upload v-if="!editedItem"
                    ref="picUpload"
                    @selected="pictureSelected"
                    :form-name="formName" :propertyName="iconPropertyName" ></picture-upload>
    <picture-upload v-if="editedItem"
                    ref="picUpload"
                    @selected="pictureSelected"
                    :form-name="formName" :editedImageSrc="editedItem.imageSrc" :propertyName="propertyName" ></picture-upload>
    <div class="card-content">
      <div class="media" style="padding-top: 15px;">
        <div class="media-content">
          <div class="field">
            <div class="control">
              <input class="input is-primary" type="text" v-model="model.title" placeholder="عنوان">
            </div>
          </div>
        </div>
      </div>

      <div class="content">
        <textarea class="textarea" placeholder="توضیحات" v-model="model.description" rows="2"></textarea>
      </div>
    </div>
    <footer class="card-footer">
      <button v-if="!editedItem" class="card-footer-item button is-white" v-on:click="addClick">
        افزودن
      </button>
      <button v-if="editedItem" class="card-footer-item button is-white" v-on:click="editClick">
        تغییر
      </button>
    </footer>
  </div>
</template>

<script lang="js">
  import PictureUpload from '../PictureUpload'

  export default {
    name: 'PictorialMenuItemCreator',
    props: {
      sampleId: 0,
      editedItem: {
        type: Object
      },
      formName: {
        type: String
      },
      iconPropertyName: {
        type: String
      },
      unknownURL: {
        default: 'data:image/png;base64,',
        type: String
      }
    },
    data: function () {
      return {
        model: {
          id: 0,
          icon: {
            base64: '',
            rotate: 0
          },
          title: '',
          description: '',
          samples_id: 0
        },
        iconSrc: '',
        sharedState: this.$store.state
      }
    },
    components: {
      PictureUpload
    },
    methods: {
      reset: function () {
        this.model = {
          id: 0,
          icon: {
            base64: '',
            rotate: 0
          },
          title: '',
          description: '',
          sampleId: 0
        };
        this.title = '';
        this.description = '';
        this.imageSrc = '';
        this.$refs.picUpload.reset();
      },
      pictureSelected: function (imageSrc) {
        this.model.icon.base64 = imageSrc;
        // console.log(imageSrc)
      },
      addClick: function () {
        // console.log(JSON.stringify(this.sharedState.forms[this.formName]))
        this.model.sampleId = this.sampleId;
        console.log(this.model)
        // this.$store.commit('setToForm', {
        //   formName: this.formName,
        //   model: this.model
        // });
        // this.$store.commit('setToForm', {formName: this.formName, propertyName: 'description', property: this.description});
        // this.$store.setToForm(this.formName, "title", this.title);
        // this.$store.setToForm(this.formName, "description", this.description);
        this.$emit("add-item", this.model);
        // this.$emit("add-item", this.sharedState.forms[this.formName]);
        this.reset();
      },
      editClick: function () {
        this.$store.setToForm(this.formName, "id", this.editedItem.id);
        this.$store.setToForm(this.formName, "title", this.title);
        this.$store.setToForm(this.formName, "description", this.description);
        this.$store.setToForm(this.formName, "imageSrc", this.imageSrc);
        this.$emit("edit-item", this.sharedState.forms[this.formName]);
        this.reset();
      }
    },
    mounted: function () {
      console.log(JSON.stringify(this.editedItem))
      if (this.editedItem) {
        this.iconSrc = this.editedItem.imageSrc;
        this.title = this.editedItem.title;
        this.description = this.editedItem.description;
      } else {
        this.title = '';
        this.description = '';
      }
    },
  };
</script>

