<template lang="html">
  <article class="media" v-bind:class="{ active: activeId == id }">
    <figure class="media-left">
      <p class="image is-64x64">
        <picture-box :image="image"></picture-box>
      </p>
    </figure>
    <div class="media-content">
      <div class="content">
        <p>
          <strong>{{title}}</strong>
          <br>
          {{description}}
        </p>
      </div>
      <!--<nav class="level is-mobile">
          <div class="level-left">
              <a class="level-item">
                  <span class="icon is-small"><i class="fas fa-reply"></i></span>
              </a>
              <a class="level-item">
                  <span class="icon is-small"><i class="fas fa-retweet"></i></span>
              </a>
              <a class="level-item">
                  <span class="icon is-small"><i class="fas fa-heart"></i></span>
              </a>
          </div>
      </nav>-->
    </div>
    <div class="media-right">
      <router-link :to="'/sample/' + sampleId + '/session/' + id" class="navbar-item">
        <i class="fa fa-2x fa-chevron-circle-left" ></i>
      </router-link>
<!--      <button class="icon is-medium" style="cursor: pointer;">-->
<!--        <i class="fa fa-2x fa-chevron-circle-left" v-on:click="sessionSelected"></i>-->
<!--      </button>-->
    </div>
  </article>
</template>

<script >
  import PictureBox from '../PictureBox'
  export default {
    name: 'PictorialMenuItem',
    props: {
      sampleId: 0,
      activeId: 0,
      id: {
        type: Number
      },
      image: {
        type: String
      },
      title: {
        type: String
      },
      description: {
        type: String
      },
      unknownURL: {
        default: 'data:image/png;base64,',
        type: String
      }
    },
    data: function () {
      return {}
    },
    components: {
      PictureBox
    },
    methods: {
      sessionSelected: function () {
        console.log("session selected!", this.id);
        this.$emit("session-selected", this.id);
      }
    }
  };
</script>

<style lang="css">
  .media-left img {
    border-radius: 50%;
  }
</style>

<style scoped lang="css">
    .media.active {
        border: 2px solid black;
        border-radius: 6px;
        padding: 12px;
        background-color: aliceblue;
    }

    button.icon {
        background-color: inherit;
        border: none;
    }

    button.icon:hover {
        background-color: #cfd7d4;
        border: 1px solid black;
        border-radius: 3px;
    }

    ::-webkit-scrollbar {
        width: 12px;
    }

    ::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
        border-radius: 10px;
    }

    ::-webkit-scrollbar-thumb {
        border-radius: 10px;
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
    }

    .media-left img {
      border-radius: 50%;
    }

    .media-content p {
      font-size: 14px;
      line-height: 2.3;
      font-weight: 700;
      color: #8F99A3;
    }

    article.post {
      margin: 1rem;
      padding-bottom: 1rem;
      border-bottom: 1px solid #E6EAEE;
    }

    article.post:last-child {
      padding-bottom: 0;
      border-bottom: none;
    }

  .router-link-active {
    color: #dd6b20 !important;
  }
</style>
